import React, { Component } from 'react'
import PlacesAutocomplete, { geocodeByAddress, getLatLng } from 'react-places-autocomplete'
import {
  Row, Col, Button, Input, CustomInput, InputGroup, InputGroupAddon,
  InputGroupText, Popover, PopoverBody
} from 'reactstrap'

let geoID

class TakeMyLocationComp extends Component {
  constructor() {
    super()
    this.state = {
      address: '',
      geolocation: false,
      coords: {},
      popoverOpen: false,
      searchBox: false
    }
    this.getCurrentLocation = this.getCurrentLocation.bind(this)
    this.togglePopover = this.togglePopover.bind(this)
    this.geoSuccess = this.geoSuccess.bind(this)
    this.geoError = this.geoError.bind(this)
  }

  handleChange = (address) => {
    this.setState({ address })
  }

  handleSelect = (address) => {
    const me = this

    this.handleChange(address)
    geocodeByAddress(address)
      .then(results => getLatLng(results[0]))
      .then(latLng => {
        me.setState({
          coords: {
            lat: latLng.lat,
            lng: latLng.lng
          }
        })
      })
      .catch(error => console.error('Error', error))
  }

  geoSuccess(position) {
    this.setState({
      geolocation: true,
      coords: {
        lat: position.coords.latitude,
        lng: position.coords.longitude
      }
    })
  }

  geoError(error) {
    console.log(error);
    navigator.geolocation.clearWatch(geoID)
    this.setState({
      geolocation: false,
      popoverOpen: true,
      coords: {}
    })
  }

  getCurrentLocation(geolocation) {
    if (geolocation) {
      this.setState({
        searchBox: true,
        address: '',
      })
      geoID = navigator.geolocation.watchPosition(this.geoSuccess, this.geoError)
    } else {
      navigator.geolocation.clearWatch(geoID)
      this.setState({
        searchBox: false,
        geolocation,
        coords: {}
      })
    }
  }

  togglePopover() {
    this.setState({ popoverOpen: !this.state.popoverOpen })
  }

  render() {
    const { search } = this.props
    const { popoverOpen, geolocation, searchBox } = this.state
    const { getCurrentLocation, togglePopover } = this

    return (
      <Row className="d-flex align-items-center">
        <Col xs="12" md="3" className="text-center text-md-right mb-3 mb-md-0">
          <p className="h5">Enter Location</p>
        </Col>
        <Col md="8">
          <PlacesAutocomplete
            value={this.state.address}
            onChange={this.handleChange}
            onSelect={this.handleSelect}
          >
            {({ getInputProps, suggestions, getSuggestionItemProps }) => (
                <div>
                  <InputGroup>
                    <Input
                      {...getInputProps({
                        placeholder: 'Search Places ...',
                        className: 'location-search-input',
                        disabled: searchBox
                      })}
                    />
                    <InputGroupAddon addonType="append">
                      <InputGroupText>
                        <CustomInput
                          type="checkbox"
                          id="myLocation"
                          label="Current Location"
                          value={geolocation}
                          onChange={event => getCurrentLocation(event.target.checked)}
                        />
                        <Popover
                          placement="bottom"
                          isOpen={popoverOpen}
                          target="myLocation"
                          toggle={togglePopover}
                        >
                          <PopoverBody>We can't get your location</PopoverBody>
                        </Popover>
                      </InputGroupText>
                    </InputGroupAddon>
                    <InputGroupAddon addonType="prepend">
                      <Button onClick={() => search()}>Go</Button>
                    </InputGroupAddon>
                  </InputGroup>
                  <div className="autocomplete-dropdown-container">
                    {suggestions.map(suggestion => {
                      const className = suggestion.active ? 'suggestion-item--active' : 'suggestion-item'
                      // inline style for demonstration purpose
                      const style = suggestion.active
                      ? { backgroundColor: '#fafafa', cursor: 'pointer' }
                      : { backgroundColor: '#ffffff', cursor: 'pointer' }
                      return (
                        <div {...getSuggestionItemProps(suggestion, { className, style })}>
                          <span>{suggestion.description}</span>
                        </div>
                      )
                    })}
                  </div>
                </div>
            )}
          </PlacesAutocomplete>
        </Col>
      </Row>
    )
  }
}

export default TakeMyLocationComp



