import React, { Component } from 'react'
import { Col, Row } from 'reactstrap'

import { Wrapper } from './../'

class ProfileComp extends Component {
  componentWillMount() {
    this.props.getProfiles()
  }

  render() {
    const { profiles } = this.props

    return (
      <Wrapper>
        <Row>
          <Col
            xs="4"
            md="2"
            className="text-center"
          >
            <img src="http://placehold.it/200x200" alt="New avatar" className="rounded-circle img-fluid" />
            <p>New Profile</p>
          </Col>
          {profiles.length > 0 && profiles.map((p, i) => (
            <Col
              key={i}
              xs="4"
              md="2"
              className="text-center"
            >
              {(p.type === 'single' && (
                <div>
                  <img src={p.picture} alt="Avatar" className="rounded-circle img-fluid"/>
                  <p>{p.name}</p>
                </div>
              )) || (
                <div>
                  <Row className="justify-content-center">
                    {p.pictures.map((g, f) => (
                      <Col
                        key={f}
                        xs={4}
                        md={3}
                        className="text-center px-2"
                        >
                        <img src={g} alt="Group" className="rounded-circle img-fluid"/>
                      </Col>
                    ))}
                  </Row>
                  <p>{p.name}</p>
                </div>
              )}
            </Col>
          ))}
        </Row>
      </Wrapper>
    )
  }
}

export default ProfileComp



