import React, { Component } from 'react'
import { connect } from 'react-redux'
import { Link } from 'react-router-dom'
import {
  Container,
  Row,
  Col,
  Button
} from 'reactstrap'

import { setRoute } from './../../redux/actions/actions'

class Wrapper extends Component {
  render() {
    const { currentRoute, setRoute } = this.props

    return (
      <Container className="mt-3">
        <Row>
          <Col>
            <Link
              to="/"
            >
              <Button
                block
                outline={currentRoute !== 0}
                color="primary"
                onClick={() => setRoute(0)}
              >
                Home
              </Button>
            </Link>
          </Col>
          <Col>
            <Link
              to="/profile"
            >
              <Button
                block
                outline={currentRoute !== 1}
                color="primary"
                onClick={() => setRoute(1)}
              >
                Profile
              </Button>
            </Link>
          </Col>
          <Col>
            <Link
              to="/settings"
            >
              <Button
                block
                outline={currentRoute !== 2}
                color="primary"
                onClick={() => setRoute(2)}
              >
                Settings
              </Button>
            </Link>
          </Col>
        </Row>
        <Row className="mt-5">
          <Col>
            {this.props.children}
          </Col>
        </Row>
      </Container>
    )
  }
}

const mapStateToProps = ({ currentRoute }) => ({ currentRoute })

const mapDispatchtoProps = (dispatch) => ({
  setRoute: route => dispatch(setRoute(route))
})

export default connect(mapStateToProps, mapDispatchtoProps)(Wrapper)



